<template>
    <Teleport to="body">
      <div v-if="show" class="modal show" @click.self="close">
        <div class="box" :style="boxStyle">
          <span class="close" @click="close">√ó</span>
          <h3 v-if="title" class="modal-title">{{ title }}</h3>
          <div class="modal-body"><slot /></div>
          <div class="modal-footer">
            <!-- ÈÄè‰º† close ÊñπÊ≥ïÁªô footer ÊèíÊßΩ -->
            <slot name="footer" :close="close" />
          </div>
        </div>
      </div>
    </Teleport>
  </template>
  
  <script setup>
  import { computed } from 'vue'
  
  const props = defineProps({
    modelValue: Boolean,
    title: String,
    maxWidth: { type: String, default: '480px' },
  })
  const emit = defineEmits(['update:modelValue'])
  const show = computed({
    get:  () => props.modelValue,
    set:  v => emit('update:modelValue', v)
  })
  const close = () => show.value = false
  
  const boxStyle = computed(() => ({
    width:   '90vw',
    maxWidth: props.maxWidth,
    padding: '24px',
    position: 'relative',
  }))
  </script>
  
  <style>
  .modal.show {
    position: fixed; inset: 0;
    background: rgba(0,0,0,0.4);
    display: flex; align-items: center; justify-content: center;
    z-index: 1000;
  }
  
  .box {
    background: var(--glass-bg);
    backdrop-filter: blur(10px);
    border-radius: 12px;
    color: var(--text-color);
  }
  
  /* È°∂ÈÉ®ÁöÑ √ó ÊåâÈíÆ */
  .close {
    position: absolute; top: 8px; right: 8px; cursor: pointer;
  }
  
  .modal-title { margin-bottom: 16px; }
  .modal-body  { margin-bottom: 24px; }
  
  /* ‚Äî‚Äî ÂÖ≥ÈîÆ üëá ÊääÊåâÈíÆÂè≥ÂØπÈΩêÔºå‰∏ª/Ê¨°Êìç‰ΩúÂàÜÂºÄ ‚Äî‚Äî */
  .modal-footer {
    display: flex;
    justify-content: flex-end;  /* Âè≥ÂØπÈΩê */
    gap: 12px;                   /* ÊåâÈíÆÈó¥Èöî */
    padding-top: 8px;
  }
  </style>
  